import { Injectable } from '@angular/core';
import { Cell } from 'src/app/models/cell';
import { Row } from 'src/app/models/row';
import { Event } from 'src/app/models/event';
import { Observable, BehaviorSubject } from 'rxjs';
import * as moment from 'moment-timezone';

@Injectable({
  providedIn: 'root'
})
export class CalendarService {

  private year = new BehaviorSubject<number>(moment().tz('Europe/Rome').year());
  private month = new BehaviorSubject<number>(moment().tz('Europe/Rome').month() + 1);
  private today = new BehaviorSubject<string>(moment().tz('Europe/Rome').format('DD-MM-YYYY'));

  year$ = this.year.asObservable();
  month$ = this.month.asObservable();
  today$ = this.today.asObservable();

  changeYear(year: number) {
    this.year.next(year);
  }
  changeMonth(month: number) {
    this.month.next(month);
  }
  changeDay(day: string) {
    this.today.next(day);
  }

  constructor() { }

  generateMonth(year: number, month: number, events: Event[]): Observable<Row[]> {

    return new Observable(observer => {
      moment.locale('it_IT');
      const baseDate = moment([year, (month - 1), 1]);
      const fromDate = baseDate.tz('Europe/Rome').startOf('month');
      const dow = fromDate.format('ddd');
      switch (dow) {
        case 'dom':
          fromDate.subtract(6, 'days');
          break;
        case 'mar':
          fromDate.subtract(1, 'days');
          break;
        case 'mer':
          fromDate.subtract(2, 'days');
          break;
        case 'gio':
          fromDate.subtract(3, 'days');
          break;
        case 'ven':
          fromDate.subtract(4, 'days');
          break;
        case 'sab':
          fromDate.subtract(5, 'days');
          break;
        case 'lun':
      }
      const result: Row[] = [];
      let row: Row;
      for (let c = 0; c < 43; c++) {
        if (c % 7 === 0) {
          if (c !== 0) {
            result.push(row);
          }
          if (c === 42) {
            break;
          }
          row = {
            cells: []
          };
        }
        const mayEvents = events.filter(e => {
          return e.when.startsWith(fromDate.format('YYYY-MM-DD'));
        });
        let hasAlarm = false;
        let hasPrescription = false;
        let hasDuePayment = false;
        if (mayEvents.length > 0) {
          for (const e of mayEvents) {
            if (e.nature === 'duepayment') {
              hasDuePayment = true;
            }
            if (e.nature === 'prescription') {
              hasPrescription = true;
            }
            if (e.nature.startsWith('alarm')) {
              hasAlarm = true;
            }
          }
        }
        const cell: Cell = {
          day: fromDate.format('D'),
          date: fromDate.format('DD-MM-YYYY'),
          hasAlarm,
          hasPrescription,
          hasDuePayment,
          currentMonth: fromDate.month() === (month - 1)
        };
        row.cells.push(cell);
        fromDate.add(1, 'days');
      }

      observer.next(result);
    });
  }
}
