import { Component, OnDestroy, Input } from '@angular/core';
import * as moment from 'moment-timezone';
import { Event } from 'src/app/models/event';
import { EventService } from 'src/app/services/event.service';
import { CalendarService } from 'src/app/services/calendar.service';
import { Row } from 'src/app/models/row';
import { Cell } from 'src/app/models/cell';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnDestroy {
  currentYear = 0;
  currentMonth = 0;
  today: string;
  entries: Event[] = [];
  fd: string;
  td: string;
  calendar: Row[];
  @Input() labelMonth = '';

  y$: Subscription;
  m$: Subscription;
  t$: Subscription;

  constructor(
    private es: EventService,
    private cs: CalendarService,
    private router: Router,
    ) {}

  getList(fromDate: string, toDate: string): Promise<Event[]> {
    return new Promise((resolve, reject) => {
      this.es.getList(fromDate, toDate).subscribe({
        next(list) {
          resolve(list);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  getCalendar(year: number, month: number, events: Event[]): Promise<Row[]> {
    return new Promise((resolve, reject) => {
      this.cs.generateMonth(year, month, events).subscribe({
        next(c) {
          resolve(c);
        },
        error(err) {
          reject(err);
        }
      });
    });
  }

  async ionViewWillEnter() {
    const now = moment();
    try {
      this.y$ = this.cs.year$.subscribe(async (year) => {
        this.currentYear = year;
        if (this.currentMonth !== 0) {
          const base = moment([this.currentYear, this.currentMonth - 1 , 1]).startOf('month');
          const fd = base.format('DD-MM-YYYY');
          base.endOf('month');
          const td = base.format('DD-MM-YYYY');
          this.entries = await this.getList(fd, td);
          this.calendar = await this.getCalendar(this.currentYear, this.currentMonth, this.entries);
        }
      });
      this.m$ = this.cs.month$.subscribe(async (month) => {
        this.currentMonth = month;
        if (this.currentYear !== 0) {
          const base = moment([this.currentYear, this.currentMonth - 1 , 1]).startOf('month');
          const fd = base.format('DD-MM-YYYY');
          base.endOf('month');
          const td = base.format('DD-MM-YYYY');
          this.entries = await this.getList(fd, td);
          this.calendar = await this.getCalendar(this.currentYear, this.currentMonth, this.entries);
          console.log(this.calendar);
        }
        switch (month) {
          case 1:
            this.labelMonth = 'Gennaio';
            break;
          case 2:
            this.labelMonth = 'Febbraio';
            break;
          case 3:
            this.labelMonth = 'Marzo';
            break;
          case 4:
            this.labelMonth = 'Aprile';
            break;
          case 5:
            this.labelMonth = 'Maggio';
            break;
          case 6:
            this.labelMonth = 'Giugno';
            break;
          case 7:
            this.labelMonth = 'Luglio';
            break;
          case 8:
            this.labelMonth = 'Agosto';
            break;
          case 9:
            this.labelMonth = 'Settembre';
            break;
          case 10:
            this.labelMonth = 'Ottobre';
            break;
          case 11:
            this.labelMonth = 'Novembre';
            break;
          case 12:
            this.labelMonth = 'Dicembre';
            break;
        }
      });
      this.t$ = this.cs.today$.subscribe(today => {
        this.today = today;
      });

    } catch (err) {
      console.log(err);
    }
  }

  dayClick(c: Cell) {
    if (c.currentMonth) {
      this.router.navigate(['day', c.date]);
    }
  }

  ngOnDestroy() {
    if (this.y$) {
      this.y$.unsubscribe();
    }
    if (this.m$) {
      this.m$.unsubscribe();
    }
    if (this.t$) {
      this.t$.unsubscribe();
    }
  }

  backMonth() {
    if (this.currentMonth > 1) {
      this.cs.changeMonth(this.currentMonth - 1);
    } else {
      this.cs.changeMonth(12);
      this.cs.changeYear(this.currentYear - 1);
    }
  }

  forwardMonth() {
    if (this.currentMonth < 12) {
      this.cs.changeMonth(this.currentMonth + 1);
    } else {
      this.cs.changeMonth(1);
      this.cs.changeYear(this.currentYear + 1);
    }
  }
}
